import Vue from 'vue';

import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';
import { ITask, IsubTask } from '~/entity/task';
import Api from '~/api';
import { IPublishedTaskParams, ITaskEvaluationParams } from '~/entity/route_params';
import Utils from '~/utils';
import { CACHE_NAME } from '~/cache';
import { ToastMsg } from '~/entity/toast';

@Component({})
export default class PublishTaskDetailComp extends Vue {
    // 我发布的任务
    publishedTask: ITask = null;
    
    @Action('showToast')
    showToast;

    async activated() {
        const params: IPublishedTaskParams = this['$route'].params;
        const res = await Api.getPublishedTaskDetail(params.taskId);
        this.publishedTask = res.body.obj;
        this.checkAmount(this.publishedTask);
        this.findAcceptLetter(this.publishedTask);
    }
    
    /**
     * 计算任务的退款金额,添加 refundAmout 字段
     * 计算任务的实付金额，添加 actuallyPaid 字段
     * 计算任务的剩余金额，添加 residualAmount 字段
     * @param task 
     */
    checkAmount(task: ITask) {
        // 初始化 退款金额 与 剩余金额
        let refundAmout = 0;
        let residualAmount = task.totalAmount;
        let residualCount = 0; // 公开任务剩余红包个数***************
        let remainRefundStatus = 0; // 公开红包剩余子任务的退款情况，-1：退款失败，0：默认，1：退款中，2：已退款***************
        let failRefundAmout = 0; // 退款失败金额***************

        for (const subTask of task.subsetTasks) {
            if (subTask.taskStatus !== 0 && subTask.taskStatus !== 4) {
                if (task.taskType === 2 && subTask.receiveStatus === 0) { // 公开任务取消子任务的退款情况，-1：退款失败，0：默认，1：退款中，2：已退款***************
                    remainRefundStatus = subTask.refundStatus;
                    failRefundAmout += subTask.amount; // 退款失败总金额***************
                }
                refundAmout += subTask.amount; // 任务已经取消或者过期，累加
                residualAmount -= subTask.amount; // 任务已经取消或者过期，可领取金额减少
            } else if (subTask.taskStatus === 4 || (subTask.taskStatus === 0 && subTask.receiveStatus === 1)) {
                residualAmount -= subTask.amount; // 任务已完成或者已领取未完成，可领取金额减少
            } else {
                residualCount++;
            }
        }

        task['refundAmout'] = refundAmout;
        task['residualAmount'] = residualAmount;  
        task['actuallyPaid'] = task.totalAmount - refundAmout;
        task['residualCount'] = residualCount;
        task['remainRefundStatus'] = remainRefundStatus;
        task['failRefundAmout'] = failRefundAmout;
    }

    /**
     * 遍历每个子任务的每个推荐信，查找其中被添加为好友的推荐信
     * 将这个推荐信 添加到子任务的 acceptLetter 属性上
     * @param task 
     */
    findAcceptLetter(task: ITask) {
        const subTasks = task.subsetTasks;
        for (const subTask of subTasks) {
            if (subTask.taskStatus === 4) {
                // subTask['acceptLetters'] = [];
                // 任务正常结束，查找添加的好友
                for (const letter of subTask.letters) {
                    if (letter.isFriend === 1) {
                        subTask['acceptLetter'] = letter;
                        break;
                    }
                }
            }
        }
    }

    /**
     * 前往任务点评页面
     * @param subTask 
     * @param action 
     */
    toEvaluatePage(subTask: IsubTask, action: string) {
        Utils.saveItem(CACHE_NAME.subTask + subTask.subId, subTask);
        const params: ITaskEvaluationParams = {
            action,
            subId: subTask.subId
        };
        this['$router'].push({
            name: 'taskEvaluation',
            params
        });
    }

    /**
     * 一次性取消所有任务
     * @param taskId 
     */
    async cancelRemainSubTask(taskId: string) {
        try {
            const res = await Api.cancelRemainSubTask(taskId);
            // 重新获取任务情况
            const res2 = await Api.getPublishedTaskDetail(taskId);
            this.publishedTask = res2.body.obj;
            this.checkAmount(this.publishedTask);
            this.findAcceptLetter(this.publishedTask);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        }
    }

    /**
     * 取消单个子任务或者再次发起退款请求
     * @param subId 
     * @param refundStatus 
     */
    async cancelSimpleSubTaskOrRefund(subId: string, refundStatus: number) {
        try {
            const res = await Api.cancelSimpleSubTaskOrRefund(subId);

           // 重新获取任务情况
           const params: IPublishedTaskParams = this['$route'].params;
           const res2 = await Api.getPublishedTaskDetail(params.taskId);
           this.publishedTask = res2.body.obj;
           this.checkAmount(this.publishedTask);
           this.findAcceptLetter(this.publishedTask);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        }
    }
}



